import * as React from 'react';
import { InlinePlaceholder } from '@ali/4ever-component';
import { Tag } from '@ali/4ever-plugin-tag';
import { RenderNodeProps } from '@ali/4ever-cangjie';
import { moFactory } from '@ali/4ever-plugin-tag';

const renderUnknownTag = {
  [Tag.TYPE]: (props: RenderNodeProps) => {
    return (<InlinePlaceholder {...props} />);
  },
}

const createUnknownTagPlugin = () => ({
  ...moFactory({}),
  renderNode: renderUnknownTag,
});

export default createUnknownTagPlugin;
